@charset "utf-8";
@import "_smart-grid";
@import "settings/_settings";


#form {
	display: flex;
	flex-direction: column;
	align-content: center;
	margin: 0 auto;
	margin-top: 50px;
	margin-bottom: 50px;

	.title__headline-form {

		p {
			margin-top: -10px;
			font-family: 'Montserrat', sans-serif;
			font-size: 35px;
			font-weight: 800;
			letter-spacing: 4px;
			text-align: center;
			text-transform: uppercase;
			color: #fff;

			@include xs(margin-top, 20px);
			@include xs(font-size, 25px);

		}

		.d {
			font-family: 'Montserrat', sans-serif;
			color: #fff;
			font-size: 20px;
			font-weight: 300;
			text-align: center;
			text-transform: none;
			letter-spacing: 0;
			margin-top: -30px;

			@include xs(font-size, 18px);
			@include xs(margin-top, -25px);
		}
	}

	.label-input {
		margin: 0 auto;
		display: flex;
		flex-direction: column;

		label {
			margin: 0 auto;
			margin-bottom: 10px;

			input {
				font-family: 'Montserrat', sans-serif;
				border-radius: 25px;
				font-size: 16px;
				width: 333px;
				padding: 10px 15px;
				border: none;
				background: #fff;
				color: rgb(0, 0, 0);


				&:focus {
					outline: none;
				}

			}

			textarea {
				font-family: 'Montserrat', sans-serif;
				border-radius: 25px;
				font-size: 16px;
				max-width: 300px;
				padding: 10px 15px;
				background: #fff;
				color: rgb(0, 0, 0);
				border: none;

				&:focus {
					outline: none;
				}
			}
		}

		.button-submit {
			margin-top: 10px;
			font-family: 'Montserrat', sans-serif;
			font-size: 13px;
			text-transform: uppercase;
			height: 40px;
			border: none;
			color: rgb(255, 255, 255);
			border-radius: 20px;
			background: #2196f3;
			font-weight: 600;
			letter-spacing: 2px;

			cursor: pointer;

			&:hover {
				background: none;
				border: 1px solid;
			}

			&:focus {
				outline: none;
			}

		}
	}
}