 @charset "utf-8";
 @import "_smart-grid";
 @import "fonts/_fonts";

 /*  ================>  Global styles <=============== */


 .СyberBionic,
 span {
 	color: #29ff16;
 	text-decoration: none;

 }

 /* ================>  Rotating Slideshow styles  <=============== */

 .rotating-slideshow {
 	background: rgb(163, 163, 163);
 	opacity: 1;
 	width: 100%;
 	height: auto;
 	max-height: 490px;
 	overflow: hidden;
 	position: relative;

 }

 .rotating-slideshow .back {
 	height: 0;
 	width: 100%;
 	max-width: 1920px;
 	margin: 0 auto;
 	position: relative;

 }

 .rotating-slideshow .button {
 	position: absolute;
 	top: 22%;
 	width: 300px;
 	height: 55%;
 	z-index: 1990;
 	opacity: .75;
 	cursor: pointer;
 }

 .rotating-slideshow .button {
 	-moz-transform: scaleX(1);
 	-o-transform: scaleX(1);
 	-webkit-transform: scaleX(1);
 	transform: scaleX(1);
 	left: 1175px;
 	right: initial;
 }

 .rotating-slideshow .button a {
 	display: block;
 	position: absolute;
 	width: 360px;
 	height: 360px;
 	transform: rotate(45deg);
 	outline: none;
 	z-index: 1990;
 	-webkit-tap-highlight-color: rgba(0, 0, 0, 0);
 }

 .rotating-slideshow .button a.pos1 {
 	top: 25%;
 	top: calc(50% - 186px);
 	left: -94%;
 	display: none;
 }

 .rotating-slideshow .button a.pos2 {
 	top: -117%;
 	left: 50%;
 	left: calc(50% - 224px);
 	width: 450px;
 	height: 450px;
 }

 .rotating-slideshow .button a.pos3 {
 	top: 25%;
 	top: calc(50% - 305px);
 	left: 65%;
 	left: calc(50% + 125px);
 	width: 620px;
 	height: 620px;
 }

 .rotating-slideshow .button a.pos4 {
 	top: 80%;
 	left: 50%;
 	left: calc(50% - 226px);
 	width: 450px;
 	height: 450px;
 }

 .rotating-slideshow #slider-main {
 	width: 100%;
 	height: 590px;
 	max-width: 1920px;
 	margin: 0 auto;
 	overflow: hidden;
 	position: relative;
 }

 .rotating-slideshow #slider-main img {
 	max-width: none;
 }

 .rotating-slideshow #slider-main .spinner {
 	position: absolute;
 	top: -172%;
 	top: -1030px;
 	left: 0;
 	right: initial;
 	min-width: 100%;
 	transition: all .5s ease, opacity .3s ease;
 	opacity: .4;
 	z-index: 99;

 }

 .rotating-slideshow #slider-main .spinner-btn {
 	position: absolute;
 	top: -172%;
 	top: -1030px;
 	left: 0;
 	min-width: 100%;
 	transition: all .5s ease, opacity .3s ease;
 	z-index: 1000;

 	@include lg (opacity, 0);
 }

 .rotating-slideshow #slider-main .slides {
 	position: relative;
 	margin: -590px auto;
 	width: 100%;
 	max-width: 1920px;
 	height: 590px;
 	display: none;
 }

 .rotating-slideshow #slider-main .slides img {
 	position: absolute;
 	top: 0;
 	left: 0;
 	opacity: 0;
 	transition: opacity 1.5s ease-out;
 	z-index: 9;
 }

 .rotating-slideshow #slider-main .slides img.active {
 	opacity: 1;
 }

 .rotating-slideshow #slider-main .slider-overlay {
 	width: 100%;
 	max-width: 980px;
 	margin: 0 auto;
 	height: 100%;
 	position: relative;
 	padding: 0 100px;
 }

 /* ================>  p h3 опции  <=============== */

 .rotating-slideshow #slider-main .slider-overlay div {
 	position: absolute;
 	top: 5%;
 	left: -100%;
 	opacity: 0;
 	transition: all .4s ease, opacity .4s ease;
 	z-index: 999;
 	color: rgb(255, 255, 255);
 	padding: 0 20px;
 	width: 80%;

 	@include xs(width, 100%);

 }

 .rotating-slideshow #slider-main .slider-overlay div.active {
 	left: 0;
 	opacity: 1;

 }

 .rotating-slideshow #slider-main .slider-overlay div h3 {
 	font-size: 1.5em;
 	font-family: 'Montserrat', sans-serif;
 	font-weight: 800;

 	@include xs(font-size, 1.3em);

 }

 .rotating-slideshow #slider-main .slider-overlay div p {
 	font-size: 1.0em;
 	font-family: 'Montserrat', sans-serif;

 	@include xs(font-size, 0.9em);
 }

 .rotating-slideshow #slider-sound {
 	display: none;
 }

 @media only screen and (max-width:1520px) {

 	.rotating-slideshow .button {
 		left: initial;
 		right: 45px;
 	}
 }