@charset "utf-8";
@import "_smart-grid";
@import "settings/_settings";


/*  ================>   Иконка burger setting <=============== */
.box__header {
	@include col();


	.menu {
		@include media;
	}

	.menu-item {
		display: inline-block;
		font-family: "Montserrat", Helvetica, sans-serif;
		font-size: 14px;
		line-height: 30px;
	}

	.menu-item:hover {
		text-decoration: underline;
	}

	.icon {
		display: none;
		width: 45px;
		height: 35px;
		position: relative;
		cursor: pointer;
	}

	.icon span {
		display: block;
		position: absolute;
		height: 9px;
		width: 100%;
		background: rgb(109, 112, 111);
		border-radius: 9px;
		left: 0;
		transform: rotate(0deg);
		transition: .25s ease-in-out;
	}

	.icon span:nth-child(1) {
		top: 0px;
	}

	.icon span:nth-child(2),
	.icon span:nth-child(3) {
		top: 13px;
	}

	.icon span:nth-child(4) {
		top: 26px;
	}

	.menu.menu-open .icon span:nth-child(1) {
		top: 18px;
		width: 0%;
		left: 50%;
	}

	.menu.menu-open .icon span:nth-child(2) {
		transform: rotate(45deg);
	}

	.menu.menu-open .icon span:nth-child(3) {
		transform: rotate(-45deg);
	}

	.menu.menu-open .icon span:nth-child(4) {
		top: 18px;
		width: 0%;
		left: 50%;
	}

	.menu.menu-open .header__burger {
		opacity: 1;
	}

}

/*  ================>  header__Logotip <=============== */

.header {
	position: fixed;
	width: 100%;
	top: 0;
	background: rgb(53, 52, 52);
	color: #ffffff;
	z-index: 9999;

	.header__title {
		font-family: "LoveYaLikeASister", Helvetica, sans-serif;
		font-size: 33px;
		cursor: pointer;
		letter-spacing: 2px;
		color: #fff;
		margin: 20px 0;

		@include xs(font-size, 25px);

		&:hover {
			transform: translate(230px);
			transition: ease 3s;
			background: none;

			@include xs(transform, translate(125px));
		}

		.header__span {
			color: rgb(109, 112, 111);
		}
	}

}

/*  ================>   Иконка мессенджера <=============== */

#burger-menui {
	background-image: linear-gradient(to right, #00cf11, #26ff00);
	font-family: 'Montserrat', sans-serif;
	font-weight: 600;
	border-radius: 20px;
	padding: 0 20px 0 16px;
	text-decoration: none;
	font-size: 14px;
	margin-left: 10px;
	color: #fff;

	@include xs(border-radius, 0);
	@include xs(margin-left, 0);

	&>i {
		padding: 0 5px;
		transform: scale(1);
		color: #fff;
	}

	&>.i {
		text-transform: uppercase;
		color: #fff;

	}
}

/*  ================>  burger-menu <=============== */

#burger-menu1,
#burger-menu2,
#burger-menu3 {
	font-size: 14px;
	letter-spacing: 2px;
	font-family: "open_sansregular", Arial, Helvetica, sans-serif;
	position: relative;
	text-decoration: none;
	margin-left: 18px;
	color: rgb(255, 255, 255);
	text-transform: uppercase;

	@include xs(font-size, 15px);
	@include xs(margin, 0);
}

#burger-menu1::before {
	content: '';
	position: absolute;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	border-bottom: 3px solid #646464;
	transition: 1s;
	transform: scaleX(0);
}

#burger-menu1:hover::before {
	transform: scale(1);
}

#burger-menu2::before {
	content: '';
	position: absolute;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	border-bottom: 3px solid #646464;
	transition: 1s;
	transform: scaleX(0);
}

#burger-menu2:hover::before {
	transform: scale(1);
}

#burger-menu3::before {
	content: '';
	position: absolute;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	border-bottom: 3px solid #646464;
	transition: 1s;
	transform: scaleX(0);
}

#burger-menu3:hover::before {
	transform: scale(1);
}