@charset "utf-8";
@import "_smart-grid";
@import "settings/_settings";


.title__portfolio {
	width: 100%;
	padding-top: 40px;
	background: rgb(255, 255, 255);

	h3 {
		font-family: "Montserrat", Helvetica, sans-serif;
		font-weight: 600;
		font-size: 30px;
		color: rgb(0, 0, 0);
		text-align: center;
		text-transform: uppercase;
		margin: 0;
		letter-spacing: 5px;
	}

}

.section__box {
	background: rgb(255, 255, 255);

	.container__box {
		@include col();
		@include size(3);
		@include size-lg(6);
		@include size-xs(12);

		margin: 2%;
		margin-bottom: 4%;
		width: 29%;

		.container__figure {
			position: relative;
			border: 0;
			display: flex;
			flex-direction: column;
			z-index: 0;
			background-image: url(../img/hi-tech.jpg);

			cursor: pointer;


			h2 {
				color: rgb(255, 255, 255);
				margin: 10% auto;
				transition: 2s;
				transform: translateX(-100px);
				opacity: 0;
				font-family: "Montserrat", Helvetica, sans-serif;
				letter-spacing: 2px;

			}

			p {
				border-radius: 20px;
				text-transform: uppercase;
				font-size: 14px;
				color: rgb(255, 255, 255);
				font-family: "Montserrat", Helvetica, sans-serif;
				margin: 10% auto;
				transition: 2s;
				transform: translateX(100px);
				opacity: 0;
				padding: 2%;
				letter-spacing: 2px;
				background: #aaa7a7;

				&:hover {
					background: #858383;
				}

			}

		}

		.container__figure:before {
			content: "";
			position: absolute;
			top: 0;
			right: 0;
			left: 0;
			bottom: 0;
			border-top: 2px solid #2196f3;
			border-bottom: 2px solid #2196f3;
			transition: all .5s ease-in;
			transform: scaleX(0);
		}

		.container__figure::after {
			content: "";
			position: absolute;
			top: 0;
			right: 0;
			left: 0;
			bottom: 0;
			border-left: 2px solid #2196f3;
			border-right: 2px solid #2196f3;
			transition: all .5s ease-in;
			transform: scaleY(0);
		}

		.container__figure:hover::after,
		.container__figure:hover::before {
			transform: scaleY(1);

		}
	}

	.container__figure:hover h2,
	.container__figure:hover p {
		transform: translateX(0);
		opacity: 1;

	}
}