@charset "utf-8";
@import "_smart-grid";

.footer {
	background: rgb(53, 52, 52);

}

.footer__box {
	@include col ();

	padding: 20px 0;

	img {
		width: 15%;
		height: 15%;

		@include xs(width, 35px);

	}
}

/*  ================>  footer__@mail <=============== */

.footer__link {
	cursor: default;
	font-size: 23px;
	text-decoration: none;
	color: #ffffff;

	@include xs(font-size, 15px);

	.span__green {
		color: #16cf06;
	}

	.span__grey {
		color: rgb(109, 112, 111);
	}

}

.span__copy {
	padding-left: 10px;
	color: rgb(245, 116, 30);
}