@charset "utf-8";
@import "_smart-grid";
@import "settings/_settings";
@import "footer/_footer";
@import "portfolio/_portfolio";
@import "Slayder/_Slayder";
@import "header/_header";
@import "form/_form";
@import "fonts/_fonts";


html {
	background: #000000;
}

body {
	height: 100%;
	background: url(../img/01.png);
	background-size: cover;
	margin: auto;
	background-attachment: fixed;

}

div,
p,
input,
form,
button,
span,
a {
	box-sizing: border-box;
}

ul,
li {
	display: block;
	padding: 0;
	margin: 0;
}

.container {
	@include wrapper();
}

.row {
	@include row-flex();
	justify-content: space-between;
	align-items: center;
}


.section-cover {
	display: flex;
	flex-direction: column;
	align-items: center;
	align-content: center;
	margin-bottom: 185px;
	margin-top: 160px;

	h1 {
		font-family: 'Montserrat', sans-serif;
		font-weight: 800;
		color: aliceblue;
		font-size: 72px;
		letter-spacing: 2px;
		text-align: center;
		padding-top: 100px;
		margin: -8px auto;

		@include xs(font-size, 30px);
	}

	h2 {
		font-family: 'Montserrat', sans-serif;
		font-weight: 200;
		font-size: 25px;
		color: aliceblue;
		text-align: center;

		@include xs(font-size, 18px);
	}


	.Button-box {
		margin-top: 50px;
		display: flex;
		justify-content: center;
		align-items: center;
		width: 100px;
		height: 100px;


		cursor: pointer;


		.Button {
			position: relative;
			width: 24px;
			height: 24px;



			.chevron {
				position: absolute;
				width: 28px;
				height: 8px;
				opacity: 0;
				transform: scale3d(0.5, 0.5, 0.5);
				animation: move 3s ease-out infinite;
			}

			.chevron:first-child {
				animation: move 3s ease-out 1s infinite;
			}

			.chevron:nth-child(2) {
				animation: move 3s ease-out 2s infinite;
			}

			.chevron:before,
			.chevron:after {
				content: ' ';
				position: absolute;
				top: 0;
				height: 100%;
				width: 51%;
				background: #fff;
			}

			.chevron:before {
				left: 0;
				transform: skew(0deg, 30deg);
			}

			.chevron:after {
				right: 0;
				width: 50%;
				transform: skew(0deg, -30deg);
			}

			@keyframes move {
				25% {
					opacity: 1;
				}

				33% {
					opacity: 1;
					transform: translateY(30px);
				}

				67% {
					opacity: 1;
					transform: translateY(40px);
				}

				100% {
					opacity: 0;
					transform: translateY(55px) scale3d(0.5, 0.5, 0.5);
				}
			}


			@keyframes pulse {
				to {
					opacity: 1;
				}
			}
		}
	}
}

#up-arrow {
	background: url("../img/up.png") no-repeat top left;
	height: 60px;
	width: 70px;
	bottom: 20px;
	right: 26px;
	cursor: pointer;
	display: none;
	position: fixed;
	z-index: 9999;

	@include xs(right, 0px);
}