@charset "utf-8";
@import "_smart-grid";


 /*  ================>  Menu-burger <=============== */
@mixin media {

	@media screen and(max-width: 780px) {

		&>.icon {
			display: inline-block;

		}

		&>.header__burger {
			position: absolute;
			display: block;
			right: 0;
			left: 0;
			margin-top: 15px;
			overflow: auto;
			opacity: 0;

			&>.menu-item {
				display: block;
				text-align: center;
				color: #fff;
				background: rgb(53, 52, 52);
			}
		}
	}

	@content
}


.fa-facebook-square {
	color: rgb(255, 255, 255);
	width: 20px;
	height: 20px;
}

.fa-instagram {
	color: rgb(255, 255, 255);
	width: 20px;
	height: 20px;
}
